.post-preview-wrapper {
  display: grid;
}

.post-preview-wrapper a{
  text-decoration: none;
}

.post-preview-wrapper:hover {
  opacity: .8;
}

.title-container {
  margin-top: 5px;
  margin-left: 5px;
  margin-right: 5px;
  background-color: #55c500;
  -webkit-box-shadow: 0px 0px 5px 1px #1c4100 inset;
          box-shadow: 0px 0px 5px 1px #1c4100 inset;
  color: #001f3f;
  border-bottom: 4px solid #001f3f;
}

.title {
  margin-left: 5px;
}

.preview-image-container {
  display: grid;
  justify-items: center;
}

.preview-img {
  max-width: 95%;
  margin-bottom: 5px;
}



